<template>
  <div>
    <div class="input-group mb-3">
      <input
        type="text"
        class="form-control"
        placeholder="Cari berdasarkan nama keluarga atau anggota keluarga"
        v-model="searchQuery"
        @input="search"
      />
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="clearSearch"
      >
        Bersihkan
      </button>
    </div>
    <div>
      <button class="btn btn-primary mb-3" @click="printPage">Print</button>
      <!-- Tabel dan kode lainnya tetap tidak berubah -->
    </div>
    <table class="table" id="zakatTable">
      <thead>
        <tr>
          <th scope="col">No</th>
          <th scope="col">uuid</th>
          <th scope="col">Keluarga</th>
          <th scope="col">Anggota Keluarga</th>
          <th scope="col">Zakat</th>
          <th scope="col">Fidyah</th>
          <th scope="col">Sodaqoh</th>
          <th scope="col">Total</th>
          <th scope="col">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(zakat, index) in displayedZakats" :key="zakat.uuid_zakat">
          <td>{{ (currentPage - 1) * pageSize + index + 1 }}</td>
          <td>{{ zakat.uuid_zakat }}</td>
          <td>{{ zakat.nama_kepala_keluarga }}</td>
          <td>
            <ul>
              <li
                v-for="(anggota, i) in zakat.anggota_keluarga"
                :key="anggota.uuid"
              >
                {{ i + 1 }}. {{ anggota.nama }}
              </li>
            </ul>
          </td>
          <td>Rp. {{ zakat.jumlah_pembayaran_zakat }}</td>
          <td>Rp. {{ zakat.jumlah_pembayaran_fidyah }}</td>
          <td>Rp. {{ zakat.jumlah_pembayaran_sodaqoh }}</td>
          <td>Rp. {{ zakat.total_bayar }}</td>
          <td>
            <button class="btn btn-danger" @click="hapusItem(zakat.uuid_zakat)">
              <i class="fas fa-trash-alt"></i> Hapus
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <nav aria-label="Page navigation">
      <ul class="pagination justify-content-center mt-4">
        <li class="page-item" :class="{ disabled: currentPage === 1 }">
          <button class="page-link" @click="goToPage(1)">First</button>
        </li>
        <li class="page-item" :class="{ disabled: currentPage === 1 }">
          <button class="page-link" @click="goToPage(currentPage - 1)">
            Previous
          </button>
        </li>
        <li
          v-for="pageNumber in totalPages"
          :key="pageNumber"
          class="page-item"
          :class="{ active: pageNumber === currentPage }"
        >
          <button class="page-link" @click="goToPage(pageNumber)">
            {{ pageNumber }}
          </button>
        </li>
        <li class="page-item" :class="{ disabled: currentPage === totalPages }">
          <button class="page-link" @click="goToPage(currentPage + 1)">
            Next
          </button>
        </li>
        <li class="page-item" :class="{ disabled: currentPage === totalPages }">
          <button class="page-link" @click="goToPage(totalPages)">Last</button>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "TabelListZakat",
  data() {
    return {
      zakats: [],
      currentPage: 1,
      pageSize: 10, // Jumlah item per halaman
      searchQuery: "",
      filteredZakats: [],
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredZakats.length / this.pageSize);
    },
    displayedZakats() {
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return this.filteredZakats.slice(startIndex, endIndex);
    },
  },
  methods: {
    setZakats(data) {
      this.zakats = data;
      this.filteredZakats = data;
    },
    search() {
      this.filteredZakats = this.zakats.filter((zakat) =>
        this.filterZakat(zakat)
      );
      this.currentPage = 1; // Set halaman ke 1 setelah pencarian
    },
    clearSearch() {
      this.searchQuery = "";
      this.filteredZakats = this.zakats;
      this.currentPage = 1; // Set halaman ke 1 setelah membersihkan pencarian
    },
    filterZakat(zakat) {
      const searchLowerCase = this.searchQuery.toLowerCase();
      return (
        zakat.nama_kepala_keluarga.toLowerCase().includes(searchLowerCase) ||
        zakat.anggota_keluarga.some((anggota) =>
          anggota.nama.toLowerCase().includes(searchLowerCase)
        )
      );
    },
    hapusItem(id) {
      // Konfirmasi penghapusan item
      if (confirm("Apakah Anda yakin ingin menghapus item ini?")) {
        // Kirim permintaan DELETE ke API menggunakan Axios
        axios
          .delete(`http://103.155.117.238:5000/input-zakat/delete/${id}`)
          .then((response) => {
            // Handle jika permintaan berhasil
            console.log("Item berhasil dihapus:", response.data);
            // Lakukan pembaruan pada tampilan aplikasi Anda, misalnya dengan menghapus item dari array zakats
            this.fetchData();
          })
          .catch((error) => {
            // Handle jika terjadi kesalahan
            console.error("Terjadi kesalahan saat menghapus item:", error);
          });
      }
    },
    goToPage(pageNumber) {
      // Pastikan nomor halaman berada dalam rentang yang valid
      if (pageNumber >= 1 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    printPage() {
  // Simpan konten tabel dalam sebuah variabel
  let printableContent = document.getElementById("zakatTable").outerHTML;

  // Hapus kolom "Action" dari konten yang akan dicetak
  printableContent = printableContent.replace(/<th scope="col">Action<\/th>/, '');

  // Waktu pencetakan
  let currentTime = new Date();
  let formattedTime = currentTime.toLocaleString('en-US');

  // Buat elemen div sementara untuk menampung konten yang akan dicetak
  let printWindow = window.open("", "_blank");
  printWindow.document.write(`
    <html>
      <head>
        <title>Cetak Zakat</title>
        <style>
          /* Gaya cetak */
          /* Sembunyikan tombol Print */
          button { display: none; }
          /* Atur ukuran font dan lebar kolom agar sesuai */
          th, td { font-size: 12px; }
          /* Hapus margin dan padding */
          body { margin: 0; padding: 0; }
          /* Beri padding pada tabel agar tercetak dengan baik */
          table { width: 100%; border-collapse: collapse; }
          th, td { border: 1px solid #000; padding: 8px; }
          /* Gaya judul */
          .judul { text-align: center; font-size: 16px; margin-bottom: 10px; }
          /* Gaya waktu cetak */
          .waktu-cetak { text-align: right; font-size: 10px; margin-top: 10px; }
        </style>
      </head>
      <body>
        <div class="judul">Daftar Zakat</div>
        ${printableContent}
        <div class="waktu-cetak">Dicetak pada: ${formattedTime}</div>
      </body>
    </html>
  `);
  
  // Panggil fungsi cetak bawaan peramban
  printWindow.document.close();
  printWindow.print();
  printWindow.close();
}
,
    fetchData() {
      axios
        .get("http://103.155.117.238:5000/input-zakat/search/all", {})
        .then((response) => this.setZakats(response.data.data))
        .catch((error) => alert("Gagal ambil data zakat " + error));
    },
  },
  mounted() {
    this.fetchData();
  },
};
</script>

<style>

@media only screen and (max-width: 600px) {
  /* Gaya untuk tabel saat ditampilkan di layar ponsel */
  .table thead {
    display: none; /* Sembunyikan header saat ditampilkan di layar ponsel */
  }

  .table, .table tbody, .table tr, .table td {
    display: block; /* Atur elemen tabel menjadi tata letak blok */
    width: 100%; /* Lebar tabel mengisi lebar layar penuh */
  }

  .table tr {
    margin-bottom: 10px; /* Beri jarak antara baris tabel */
    border: 1px solid #ccc; /* Tambahkan garis antara baris */
  }

  .table td {
    text-align: left; /* Geser teks ke kiri untuk sel tabel */
    padding: 8px; /* Beri jarak di sekitar isi sel tabel */
  }
  @media only screen and (max-width: 600px) {
  /* Gaya untuk navigasi tabel saat ditampilkan di layar ponsel */
  .pagination {
    flex-wrap: wrap; /* Mengatur wrap item agar cocok dengan lebar layar */
  }

  .pagination li {
    margin-bottom: 5px; /* Beri jarak antara setiap item navigasi */
  }

  .pagination button {
    min-width: auto; /* Sesuaikan lebar tombol dengan konten */
  }
}

}


</style>
