<template>
  <div class="Container">
    <NavbarZakat />
    <div class="container">
      <div
        class="container d-flex justify-content-center align-items-center vh-100"
      >
        <div>
          <h2 class="mb-4">Data Zakat</h2>
          <p>Jumlah keluarga membayar zakat: {{ kesimpulan.jumlahKeluarga }} keluarga </p>
          <p>Jumlah jamaah membayar zakat: {{ kesimpulan.jumlahJamah }} Orang</p>
          <p>Jumlah seluruh zakat:Rp. {{ kesimpulan.jumlahSeluruhZakat }} </p>
          <p>Jumlah seluruh sodakoh: Rp. {{ kesimpulan.jumlahSeluruhSodakoh }}</p>
          <p>Jumlah seluruhnya: Rp. {{ kesimpulan.jumlahSeluruhnya  }}</p>
        </div>
      </div>

      <TabelListZakat/>

      
    </div>
  </div>
</template>

<script>
import NavbarZakat from "@/components/NavbarZakat.vue";
import TabelListZakat from "@/components/TabelListZakat.vue";
import axios from "axios";
import { getCookie } from "../cookieUtils.js"
export default {
  name: "ListZakat",
  components: {
    NavbarZakat,
    TabelListZakat
  },
  data() {
    return {
      kesimpulan: [],
    };
  },
  methods: {
   
    setKesimpulan(data) {
      this.kesimpulan = data;
    },
  },
  mounted() {
    const token = getCookie("token");
    if (!token) {
      this.$router.push('/login');
    } 
    
    axios
      .get("http://103.155.117.238:5000/input-zakat/search/kesimpulan/", {})
      .then((response) => this.setKesimpulan(response.data.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style></style>
