<template>
  <div class="Container">
    <NavbarZakat/>
    <div class="container d-flex justify-content-center align-items-center" style="height: 100vh;">
  <div>
    <h2 class="text-center mb-4">Login</h2>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="exampleInputEmail1">username:</label>
        <input type="text" class="form-control" id="exampleInputEmail1" v-model="formData.email" required>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Password:</label>
        <input type="password" class="form-control" id="exampleInputPassword1" v-model="formData.password" required>
      </div>
      <button type="submit" class="btn btn-primary btn-block">Login</button>
    </form>
    <p v-if="errorMessage" class="text-danger mt-3">{{ errorMessage }}</p>
  </div>
</div>

  </div>
</template>

<script>
import axios from "axios";
import NavbarZakat from "@/components/NavbarZakat.vue";

export default {
  name: "LoginUser",
  components:{
    NavbarZakat,
  },

  data() {
    return {
      formData: {
        email: "",
        password: "",
      },
      errorMessage: "",
    };
  },
  methods: {
    login() {
      axios
        .post("http://103.155.117.238:5000/auth/login", this.formData)
        .then((response) => {
          if (response.data.status === "ok") {
            const { token } = response.data.data;
            this.saveTokenToCookie(token);
            // Redirect to input zakat page
            this.$router.push("/ListZakat");
          } else {
            this.errorMessage = response.data.message;
          }
        })
        .catch((error) => {
          this.errorMessage = "Failed to login";
          console.error("Login error:", error);
        });
    },
    saveTokenToCookie(token) {
      const expiryDate = new Date();
      expiryDate.setDate(expiryDate.getDate() + 1); // Token expires in 1 day
      document.cookie = `token=${token}; expires=${expiryDate.toUTCString()}; path=/`;
    },
  },
};
</script>

<style>
/* CSS styling for the login form */
</style>
