<template>
  <div>melakukan logout</div>
</template>

<script>
export default {
  name: "LogoutView",
  mounted() {
    // Hapus cookie dengan nama "token"
    this.deleteCookie("token");
    
    // Arahkan pengguna ke halaman utama
    this.$router.push('/');
  },
  methods: {
    deleteCookie(name) {
      // Atur ulang nilai cookie dengan nama yang sama untuk menghapusnya
      document.cookie = `${name}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
    }
  }
}
</script>

